import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { getETHId, ADDRESS0 } from 'sdk'
import { getNftPayTokens } from 'sdk/nft/shop'
import { Heading } from 'uikit'
import Loader from 'components/Loader'
import { useAppDispatch } from 'state'
import { useNftOnsells, useNftOnsellPageQC } from 'state/hooks'
import { setOnSellPageQCVersion, setOnSellPageQCSelectedPayToken, setOnSellPageQCDesc, fetchAsksAsync, fetchPayTokenAsksAsync } from 'state/actions'
import { useTranslation } from 'contexts/Localization'
import Page from 'components/layout/Page'
import NftSellPageTab from './NftSellPageTab'
import NftSellPageContent from './NftSellPageContent'               

const StyledHeader = styled.div`
  border-bottom: 2px solid ${({ theme }) => theme.colors.textSubtle};
  margin-bottom: 12px;
  padding-bottom: 22px;
`
const ControlContainer = styled.div`
  display: flex;
  width: 100%;
  align-items: center;
  position: relative;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 20px;

  ${({ theme }) => theme.mediaQueries.sm} {
    flex-direction: row;
    flex-wrap: wrap;
    padding: 16px 20px;
    margin-bottom: 0;
  }
`
const NftSellPage : React.FC = () => {
  const { t } = useTranslation()

  const paytokens = getNftPayTokens()

  const options = [{
    label: 'All',

    value: '*',
  },
  {
    label: getETHId(),

    value: ADDRESS0,
  }].concat(paytokens.map((token) =>
  {
     return {
       label: token.symbol,

       value:  token.address
     }
  }))

  const dispatch = useAppDispatch()

  const queryConditions = useNftOnsellPageQC()

  function handlePayTokenChange(selected) {
    if(selected !== queryConditions.selectedPayToken)
    {
      dispatch(setOnSellPageQCSelectedPayToken(selected))

       setIsLoading(true)
    }
  }

  function handleDescChange()
  {
    dispatch(setOnSellPageQCDesc(!queryConditions.desc))

    setIsLoading(true)
  }

  function onRefresh()
  {
    dispatch(setOnSellPageQCVersion())

    setIsLoading(true)
  }

  const pageModule = useNftOnsells(queryConditions.version)

  const [isloading, setIsLoading] = useState(!pageModule || !pageModule.module)

  function onCommitSuccess()
  {
    queryConditions.version += 2

    setIsLoading(true)
  }

  useEffect(() => {
      if (!pageModule || (!pageModule.module && !pageModule.loading))
      {  
        if(queryConditions.paytoken === '*')
        {  
          dispatch(fetchAsksAsync(queryConditions.nft, queryConditions.version, queryConditions.page, queryConditions.pagesize, queryConditions.desc))
         .then(()=>
          {
            setIsLoading(false)
          })
        }

        else 
        {
          dispatch(fetchPayTokenAsksAsync(queryConditions.nft, queryConditions.version, queryConditions.paytoken, queryConditions.page, queryConditions.pagesize, queryConditions.desc))
          .then(()=>
           {
             setIsLoading(false)
           })        
        }
     }
    }, [dispatch, pageModule, queryConditions, setIsLoading])

  const renderContent = (): JSX.Element => {
    return ( isloading ? <Loader /> : <NftSellPageContent onCommitSuccess={onCommitSuccess} nft={queryConditions.nft} asks={pageModule?.module.asks}/>
    )
  }

  return (
    <Page>
      <StyledHeader>
        <Heading as="h1" scale="xxl" color="secondary" mb="24px">
          {t('NFT Sell')}
        </Heading>
      </StyledHeader>
      
      <ControlContainer>
        <NftSellPageTab onRefresh={onRefresh} isDesc={queryConditions.desc} handleDescChange={handleDescChange} selectPayToken={queryConditions.selectedPayToken} handlePayTokenChange={handlePayTokenChange} options={options}/>
      </ControlContainer>

      {renderContent()}
    </Page>
  )
}

export default NftSellPage