import React from 'react'
import { nftOnSellNode } from 'state/types'
import NftSellPageGrid from './NftSellPageGrid'
import NftSellPageCard from './NftSellPageCard'

interface NftSellPageContentProps {
    nft: string,
    asks: nftOnSellNode[],
    onCommitSuccess: () => void
}

const NftSellPageContent : React.FC<NftSellPageContentProps> = ({ nft, asks, onCommitSuccess }) => {
    const cards = asks ? asks.map((ask) => {  
      const params = {
        nftKey: nft, 
        
        owner: ask.owner, 
        
        paytoken: ask.paytoken, 
        
        tokenid: ask.tokenid, 
        
        price: ask.price,
      }

      return (
       <div key={nft}>
         <NftSellPageCard params={JSON.stringify(params)} refresh={onCommitSuccess}/>
      </div>
      )
     })  : [<>not found</>]

    return <NftSellPageGrid>  
            {cards} 
           </NftSellPageGrid> 
}

export default NftSellPageContent