import React from 'react'
import Select from 'react-select'
import {
  Text,
  Flex,
  Toggle,
  Button
} from 'uikit'
import { useTranslation } from 'contexts/Localization'

const NftSellPageTab = ({ selectPayToken, handlePayTokenChange, isDesc, handleDescChange, options, onRefresh }) => {
  const { t } = useTranslation()

  const selectStyles = {
    container: styles => ({ ...styles,  width: 100 })
  }

  return (
    <Flex alignItems="center" justifyContent="center" mb="10px">
      <Flex alignItems="center" flexDirection={['column', null, 'row', null]}>
       <Text fontSize="16px" bold mr='12px' color="textSubtle">
         {t('Pay Token')}: 
       </Text>
       <Select styles={selectStyles} value={selectPayToken} onChange={handlePayTokenChange} options={options}/>
      </Flex>

      <Flex ml='20px' justifyContent="center" alignItems="center">
        <Toggle scale="sm" checked={isDesc} onChange={handleDescChange} />
        <Text ml="8px">{t('Desc')}?</Text>
      </Flex>

      <Flex ml='20px' justifyContent="center" alignItems="center">
        <Button onClick={onRefresh}>
         {t('Refresh')}
        </Button>
      </Flex>
    </Flex>
  )
}

export default NftSellPageTab
