import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { getNftPayTokens } from 'sdk/nft/shop'
import { getETHId, ADDRESS0 } from 'sdk'
import {
  Card,
  CardBody,
  Text,
  Flex,
  Tag
} from 'uikit'
import Loader from 'components/Loader'
import { useActiveWeb3React } from 'hooks'
import { useTranslation } from 'contexts/Localization'
import { getBalanceNumber } from 'utils/formatBalance'
import { useAppDispatch } from 'state'
import { fetchNftMetaAsync } from 'state/actions'
import { useNftMeta, useGetApiPrices } from 'state/hooks'
import NftMediaPreviewElement from '../NftPreview/NftMediaPreviewElement'
import history from '../../routerHistory'

export interface NftSellPageCardProps {
  params: string

  refresh?: () => void
}

const RootContainer = styled.div`
  width: 100%;
`
const Container = styled.div`
  background-color: ${({ theme }) => theme.colors.cardBorder};
  position: relative;
  width: 100%;
  overflow: hidden;
  padding-bottom: 100%;
`
const NftSellPageCardElement: React.FC<NftSellPageCardProps> = ({ params }) => {
  const { nftKey, owner, paytoken, tokenid, price } = JSON.parse(params)

  const { t } = useTranslation()

  const { account } = useActiveWeb3React()

  const isOwner = account === owner

  const nftModeMeta = useNftMeta(nftKey, tokenid)

  const [isloading, setIsLoading] = useState(!nftModeMeta || (!nftModeMeta.meta && !nftModeMeta.loading))

  const dispatch = useAppDispatch()

  useEffect(() => {
    if (!nftModeMeta || (!nftModeMeta.meta && !nftModeMeta.loading))
    {  
      dispatch(fetchNftMetaAsync(nftKey, tokenid, nftModeMeta?.uri))
     .then(()=>
      {
        setIsLoading(false)
      })
    }
  }, [dispatch, nftModeMeta, nftKey, tokenid, setIsLoading])

  const handleNftonSell = () => {
    history.push(`/nftonsell/${nftKey}/${tokenid}`)
  }

  const prices = useGetApiPrices()

  const { ethPrice } = prices

  const paytokens = getNftPayTokens()

  const priceTokenPrice = paytoken === ADDRESS0 ? ethPrice : prices[paytoken]

  const priceTokenSymbol = paytoken === ADDRESS0 ? getETHId() : paytokens.find((token) => token.address === paytoken).symbol

  const priceNumber = paytoken === ADDRESS0 ? getBalanceNumber(price, 18) : getBalanceNumber(price, paytokens.find((token) => token.address === paytoken).decimals)

  const priceDollar = priceNumber * priceTokenPrice

  const isHighValue = Math.round(priceDollar * 1000) < 1

  return ( <RootContainer>
    <Card isActive>
      {isloading ? ( <Loader/>) : (<Container onClick={handleNftonSell}>
        <NftMediaPreviewElement meta={nftModeMeta?.meta}/> 
      </Container>) }

      <CardBody>
        <Flex flexDirection="column">
          <Text color="textSubtle" fontSize="16px" bold mb='10px'>
           {nftModeMeta?.meta?.name}{isOwner && <Tag ml='10px'>{t('Owner')}</Tag>}
          </Text>
          <Text color="textSubtle" fontSize="12px" bold>
           {`${priceNumber} ${priceTokenSymbol} â‰ˆ ${priceDollar.toFixed(isHighValue ? 4 : 2)} $`}
          </Text>
        </Flex>
      </CardBody>
    </Card>
    </RootContainer>
  )
}

const NftSellPageCard = React.memo(NftSellPageCardElement, (prev, next) => prev.params === next.params);

export default NftSellPageCard
